export default {
  props: {
    grandtotal: {
      type: Number,
      required: true
    }
  },

  computed: {
    store() {
      return this.$store.state.store
    },

    customized_values() {
      return this.$store.state.customized_values
    },

    customized_values_total() {
      let total = 0

      if (this.customized_values) {
        this.customized_values.forEach(el => {
          total += el.total
        })
      }

      return total
    },

    rp_order() {
      return this.$store.state.rp_order
    }
  }
}
